let shop_id = '' //商家id
export default {
    onShow(){
        // this.getshops()
    },
    onLoad(option ) {
        if(option.shop_id) {
            shop_id = option.shop_id
        }
        this.getshops()
    },
    data(){
        return {
            shopinfo:''
        }
    },

    methods: {
        getshops(){
            this.$http.getshops({shop_id}).then(res=>{
                if(res.status == 200) {
                    this.shopinfo = res.data
                }
            })
        }
    },
}
